<template>
  <v-stepper v-model="e1">
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1">Levels and stats</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2">Money and loyalties</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 3" step="3">Hideout</v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 4" step="4">Quests</v-stepper-step>

      <v-stepper-step step="3">Finish</v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        Fill in you level and skills. Some quests and hideout upgrades need certain skills.
        <v-row>
          <v-col cols="6">
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Skill</th>
                    <th class="text-left">Type</th>
                    <th class="text-left">Level</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="item in skills.filter(s => s.type==='physical' || s.type ==='main')"
                    :key="item.name"
                  >
                    <td>
                      <img :src="item.img" class="small" />
                      {{ item.name }}
                    </td>
                    <td>{{ item.type }}</td>
                    <td>
                      <v-text-field
                        v-model="stats[item.id]"
                        dense
                        type="number"
                        full-width
                        style="height: 45px; width: 40px;"
                        va
                      ></v-text-field>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
          <v-col>
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Skill</th>
                    <th class="text-left">Type</th>
                    <th class="text-left">Level</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in skills.filter(s => s.type==='mental')" :key="item.name">
                    <td>
                      <img :src="item.img" class="small" />
                      {{ item.name }}
                    </td>
                    <td>{{ item.type }}</td>
                    <td>
                      <v-text-field
                        v-model="stats[item.id]"
                        dense
                        type="number"
                        full-width
                        style="height: 45px; width: 40px;"
                        va
                      ></v-text-field>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="10"></v-col>
          <v-col cols="2">
            <v-btn color="primary" @click="e1++">Continue</v-btn>
          </v-col>
        </v-row>
      </v-stepper-content>

      <v-stepper-content step="2">
        Fill in your money and loyalties with traders. Some quests and hideout upgrades require money or loyalty instead of items.
        <v-row>
          <v-col cols="6">
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Currency</th>
                    <th class="text-left">Amount</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="currency in currencies" :key="currency.id">
                    <td>{{ currency.name }} ({{currency.symbol}})</td>
                    <td>
                      <v-text-field
                        v-model="money[currency.id]"
                        dense
                        type="number"
                        full-width
                        style="height: 45px;"
                        va
                      ></v-text-field>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
          <v-col>
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Trader</th>
                    <th class="text-left">Loyalty</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="trader in traders" :key="trader.id">
                    <td>
                      <img :src="trader.img" class="small" />
                      {{ trader.name }}
                    </td>
                    <td>
                      <v-text-field
                        v-model="loyalties[trader.id]"
                        dense
                        type="number"
                        full-width
                        style="height: 45px;"
                        va
                      ></v-text-field>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="10">
            <v-btn color="primary" @click="e1--">Back</v-btn>
          </v-col>
          <v-col cols="2">
            <v-btn color="primary" @click="e1++">Continue</v-btn>
          </v-col>
        </v-row>
      </v-stepper-content>

      <v-stepper-content step="3">
        Check all hideout upgrades you have already done.
        <v-treeview
          v-model="doneHideout"
          class="trp-light"
          selected-color="#9a8866"
          dense
          color="#f00"
          :items="hideout"
          selection-type="independent"
          open-all
          hoverable
          transition
          selectable
        >
          <template slot="label" slot-scope="{ item }">
            <v-card outlined>
              <h4>{{item.name}}</h4>
            </v-card>
          </template>
        </v-treeview>
        <v-row>
          <v-col cols="10">
            <v-btn color="primary" @click="e1--">Back</v-btn>
          </v-col>
          <v-col cols="2">
            <v-btn color="primary" @click="e1++">Continue</v-btn>
          </v-col>
        </v-row>
      </v-stepper-content>
      <v-stepper-content step="4">
        Pick your current Quests, the assistent will pick all previous quests for you.
        <v-autocomplete
          v-model="activeQuests"
          :search-input="search"
          :items="quests"
          @change="search = ''"
          chips
          color="blue-grey lighten-2"
          label="Current active quests"
          item-text="name"
          item-value="id"
          multiple
        >
          <template v-slot:selection="data">
            <v-chip
              :input-value="activeQuests"
              close
              class="chip--select-multi"
              @click:close="remove(data.item.id)"
            >{{ data.item.name }}</v-chip>
          </template>
          <template v-slot:item="data">
            <template v-if="typeof data.item !== 'object'">{{data.item}}</template>
            <template v-else>
              {{data.item.name}}
              ({{data.item.from}})
            </template>
          </template>
        </v-autocomplete>
        Based on your current active quests, {{doneQuests.length}} quests have been marked as done.
        <v-treeview
          v-model="doneQuestsShow"
          class="trp-light"
          selected-color="#9a8866"
          item-disabled="asd"
          dense
          :items="starterQuests"
          aria-readonly
          hoverable
          transition
          selection-type="independent"
          selectable
          v-if="showTree"
        >
          <template slot="label" slot-scope="{ item }">
            <v-card outlined>
              <h4>{{item.name}} ({{item.from}})</h4>
            </v-card>
          </template>
        </v-treeview>
        <v-row>
          <v-col cols="10">
            <v-btn color="primary" @click="e1--">Back</v-btn>
          </v-col>
          <v-col cols="2">
            <v-btn color="primary" @click="e1++">Continue</v-btn>
          </v-col>
        </v-row>
      </v-stepper-content>
      <v-stepper-content step="5">
        Finish the setup by clicking the button below.
        <br />
        <code>This will overwrite all previous data!</code>
        <br />
        <br />Check if your changes had the desired result and press
        <kbd>Save to cookie!</kbd> to make these changes persistent.
        <v-row v-if="!done">
          <v-col cols="10">
            <v-btn color="primary" @click="e1--">Back</v-btn>
          </v-col>
          <v-col cols="2">
            <v-btn color="primary" @click="finish">Finish</v-btn>
          </v-col>
        </v-row>
        <v-row v-if="done">
          <v-col cols="10">
            <v-btn color="primary" @click="init">Reset</v-btn>
          </v-col>
          <v-col cols="2">Changes saved.</v-col>
        </v-row>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>
<script>
export default {
  data: () => ({
    e1: 1,
    stats: {},
    money: {},
    loyalties: {},
    quests: [],
    starterQuests: [],
    doneHideout: [],
    activeQuests: [],
    doneQuestsShow: [],
    showTree: false,
    search: "",
    done: false
  }),
  methods: {
    customFilter(item, queryText) {
      console.log(item, queryText);
      return item.name.toLowerCase().includes(queryText.toLowerCase());
    },
    remove(item) {
      console.log(item, this.activeQuests);

      const index = this.activeQuests.indexOf(item);
      if (index >= 0) this.activeQuests.splice(index, 1);
    },
    finish() {
      this.$store.commit("setLevels", this.stats);
      this.$store.commit("setLoyalties", this.loyalties);
      this.$store.commit("setMoney", this.money);
      this.$store.commit("setDoneQuests", this.doneQuests);
      this.$store.commit("setDoneHideout", this.doneHideout);
      this.done = true;
    },
    init() {
      this.done = false;
      this.e1 = 1;
      this.quests = this.staticData.quests;
      this.starterQuests = this.staticData.getStarterQuests();
      this.skills = this.staticData.skills;
      this.currencies = this.staticData.currencies;
      this.traders = this.staticData.traders;
      this.hideout = this.staticData.getStarterHideout();
      this.stats = {
        level: 0,
        attention: 0,
        charisma: 0,
        endurance: 0,
        health: 0,
        immunity: 0,
        intellect: 0,
        memory: 0,
        metabolism: 0,
        perception: 0,
        strength: 0,
        stress_resistance: 0,
        vitality: 0
      };
      this.money = {
        rouble: 0,
        euro: 0,
        dollar: 0,
        bitcoin: 0,
        gp_coin: 0
      };

      this.loyalties = {
        prapor: 0,
        therapist: 0,
        fence: 0,
        skier: 0,
        peacekeeper: 0,
        mechanic: 0,
        ragman: 0,
        jaeger: 0
      };
    }
  },
  computed: {
    doneQuests: function() {
      console.log("recalc");
      var predecessors = this.staticData
        .getQuestPredecessors(this.activeQuests)
        .map(q => q.id);
      //   this.doneQuestsShow = predecessors;
      return predecessors;
    }
  },
  watch: {
    activeQuests: function(newVal) {
      this.showTree = false;
      console.log("reset", newVal);
      this.$nextTick(() => {
        this.search = null;
        this.doneQuestsShow = this.doneQuests;
        this.showTree = true;
      });
    }
  },
  created: function() {
    this.init();
  }
};
</script>
<style scoped>
img.small {
  max-width: 30px;
  max-height: 30px;
  vertical-align: middle;
  margin-right: 10px;
}
</style>